.forgot {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.forgotForm {
    width: 380px;
    padding: 1.5rem;
    box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.2);
    background: #fff;
}

.forgotTitle {
    text-align: center;
    margin-bottom: 1rem;
}

.forgotText {
    font-size: 0.7rem;
    display: block;
    margin: 0.5rem 0;
}